/* eslint-disable */
import area from './area'
const provinceList = area.filter((item) => {
    return !item.pid
})

export const inProvinceList = (() => {
    let str = '^'
    provinceList.forEach((item, index) => {
        str += item.text + '|'
    })
    str = str.slice(0, -1)
    const reg = new RegExp(str)
    console.log(reg)
    return (val) => {
        return reg.test(val)
    }
})()




