<template>
  <el-dialog :title="title" :visible.sync="dialogVisible" width="50%" :before-close="cancel">
    <div class="row-list">
      <span class="name">第三方订单编号：</span>
      <el-input v-model="data.orderNum" :disabled="true"/>
    </div>
    <div class="row-list">
      <span class="name">收货人姓名：</span>
      <el-input v-model="data.userName" :disabled="true"/>
    </div>
    <div class="row-list">
      <span class="name">收货人手机号：</span>
      <el-input v-model="data.mobile"/>
    </div>
    <div class="row-list">
      <span class="name">收货人地址：</span>
      <el-input v-model="data.address" :disabled="true"/>
    </div>
    <div class="row-list">
      <span class="name">收货人地址：</span>
      <el-select class="select" v-model="province" placeholder="省" @change="provinceHandle">
        <el-option
          v-for="(item, index) in provinceList"
          :key="index"
          :label="item.text"
          :value="item.id"
        ></el-option>
      </el-select>
      <el-select class="select" v-model="city" placeholder="市" @change="cityHandle">
        <el-option
          v-for="(item, index) in cityList"
          :key="index"
          :label="item.text"
          :value="item.id"
        ></el-option>
      </el-select>
      <el-select class="select" v-model="area" placeholder="区" @change="areaHandle">
        <el-option
          v-for="(item, index) in areaList"
          :key="index"
          :label="item.text"
          :value="item.id"
        ></el-option>
      </el-select>
    </div>
    <div class="row-list">
      <span class="name">详细地址：</span>
      <el-input type="text" placeholder="详细地址" v-model="detailAddress"/>
    </div>
    <!-- <div class="row-list">
            <span class="name">商品名：</span>
            <el-input v-model="data.excelGoodsName" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">买家账号：</span>
            <el-input v-model="data.buyAccount" :disabled="true"/>
    </div>-->
    <span slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
import area from "../utils/area";
export default {
  props: ["data", "title"],
  data() {
    return {
      dialogVisible: true,
      province: "",
      city: "",
      area: "",
      provinceList: [],
      cityList: [],
      areaList: [],
      cityIndex: 0,
      areaIndex: 0,
      detailAddress: ""
    };
  },
  created() {
    this.provinceList = this.getAreaList();
  },
  methods: {
    getAreaList(id) {
      return area.filter(item => {
        return item.pid == id;
      });
    },
    cancel() {
      this.$emit("cancel");
    },
    confirm() {
      const mobile = this.data.mobile;
      const regMobile = /^1\d{10}$/;
      const regPhone = /^\d{3,4}-\d{8}$/;
      if (regMobile.test(mobile) || regPhone.test(mobile)) {
      } else {
        this.$message("请输入正确的手机号或电话号");
        return;
      }
      const isAddressSelect =
        this.provinceText && this.cityText && this.areaText;
      if (!isAddressSelect) {
        this.$message("请选择省市区");
        return;
      }

      if (!this.detailAddress) {
        this.$message("请填写地址详情");
        return;
      }
      this.data.address =
        this.provinceText + this.cityText + this.areaText + this.detailAddress;
      this.$emit("confirm", this.data);
    },
    findCurrent(curArea, id) {
      return curArea.find(item => {
        return item.id === id;
      });
    },
    provinceHandle(val) {
      this.cityList = this.getAreaList(val);
      this.provinceText = this.findCurrent(this.provinceList, val).text;
      this.city = "";
      this.area = "";
    },
    cityHandle(val) {
      this.areaList = this.getAreaList(val);
      this.cityText = this.findCurrent(this.cityList, val).text;
      this.area = "";
    },
    areaHandle(val) {
      this.areaText = this.findCurrent(this.areaList, val).text;
    }
  }
};
</script>

<style lang="less" scoped>
.row-list {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  .name {
    width: 150px;
  }
  &:last-child {
    margin: 0;
  }
}
.select {
  width: 33%;
}
</style>



